.footer {
  width: 100%;
  background: #2694990a;
  justify-content: center;
  align-items: center;
  display: flex;
}

.footer__content {
  max-width: 1600px;
  justify-content: space-around;
  padding: 70px 0 78px;
  display: flex;
}

.footer__content__contacts {
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.footer__content__contacts__title {
  letter-spacing: .06em;
  color: #000;
  text-shadow: 1px 0 10px #000;
  font-family: Ribeye;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 36px;
}

.footer__content__contacts__subtitle {
  letter-spacing: .06em;
  color: #000;
  font-family: Roboto;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 26px;
  text-decoration: none;
}

.footer__content__contacts__social {
  width: 161px;
  justify-content: space-between;
  display: flex;
}

.footer__content__contacts__social img {
  cursor: pointer;
  transition: all .5s;
}

.footer__content__contacts__social img:hover {
  transform: scale(2);
}

.footer__content__menu {
  margin-left: 115px;
}

.footer__content__menu p {
  letter-spacing: .06em;
  color: #000;
  cursor: pointer;
  font-family: Roboto;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  transition: all .5s;
}

.footer__content__menu p:hover {
  transform: scale(1.2);
}

.footer__content__form {
  margin-left: 133px;
}

.footer__content__form p {
  letter-spacing: .06em;
  color: #000;
  font-family: Roboto;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 30px;
}

.footer__content__form input {
  width: 195px;
  height: 44px;
  opacity: .1;
  box-sizing: border-box;
  border: 1px solid #000;
  border-radius: 5px;
  box-shadow: 0 0 10px 10px #00000026;
}

.footer__content__form input::placeholder {
  letter-spacing: .06em;
  color: #000;
  font-family: Roboto;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}

.footer__content__form__button {
  width: 103px;
  height: 44px;
  letter-spacing: .06em;
  color: #fff;
  cursor: pointer;
  background: #c9453b;
  border: none;
  border-radius: 5px;
  margin-left: 35px;
  font-family: Roboto;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  transition: all .5s;
}

.footer__content__form__button:hover {
  color: #c9453b;
  background: #adff2f;
}

@media screen and (max-width: 1200px) {
  .footer__content div:nth-child(3) {
    display: none;
  }
}

@media screen and (max-width: 900px) {
  .footer__content div:nth-child(3), .footer__content div:nth-child(2) {
    display: none;
  }

  .footer__content {
    flex-flow: column wrap;
    align-items: center;
    display: flex;
  }

  .footer__content__contacts {
    align-items: center;
    display: flex;
  }

  .footer__content__contacts__subtitle, .footer__content__contacts__social {
    margin-top: 15px;
  }

  .footer__content__form {
    flex-flow: column wrap;
    align-items: center;
    margin: 0;
    display: flex;
  }

  .footer__content__form p {
    text-align: center;
  }

  .footer__content__form input {
    margin-bottom: 15px;
  }

  .footer__content__form__button {
    margin: 0;
  }
}

